.header {
  display: flex ;
  align-items: center ;
  position: fixed ;
  top: 0 ;
  left: 0 ;
  right: 0 ;
  background: rgb(0, 0, 0) ;
  color: rgb(97, 219, 251) ;
  font-size: 20px ;
  padding: 0 20px ;
  z-index: 10000 ;
  user-select: none ;

  h3 {
    cursor: pointer ;
  }

  &--logo {
    width: 60px ;
    margin-right: 10px ;
    cursor: pointer ;
  }
}