@import 'utils' ;

$primary-color: rgb(255, 255, 255) ;
$secondary-color: rgb(15, 15, 15) ;
$border: addBorder(1px, rgb(0, 0, 0)) ;

.main {
  margin: 80px 20px 0 20px ;

  &--wrapper {
    display: flex ;
    justify-content: space-between ;
    gap: 10px ;
  }

  &--search-input {
    width: 100% ;
    padding: 12px ;
    border: $border ;
    border-radius: 5px ;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5) ;
  }
}

.notes {
  margin-top: 10px ;
  flex: .75 ;

  &--dimmed {
    opacity: 0.3 ;
  }  

  &--buttons {
    display: flex ;
    justify-content: space-between ;
    align-items: center ;
    margin: 0 5px ;

    span {
      font-weight: bold ;
      font-size: 26px ;
      text-transform: uppercase ;
    }

    button {
      font-size: 20px ;
      border: none ;
      border-radius: 3px ;
      padding: 3px 8px ;
      cursor: pointer ;
    }

    .notes--add-button {
      background: rgb(224, 63, 216) ;
      color: $primary-color ;
    }

    .notes--remove-button {
      background: rgb(209, 26, 42) ;
      margin-left: 5px ;
    }
  }
}

.note {
  display: flex ;
  justify-content: center ;
  margin: 10px 0 ;
  width: 100% ;
  padding: 10px ;
  border: 1px solid $secondary-color ;
  border-radius: 10px ;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5) ;
  cursor: pointer ;

  &-active {
    background: $secondary-color ;
    color: $primary-color ;
  }

  &--body-textarea {
    margin-top: 10px ;
  }
}

.note--text {
  width: 250px ;
  text-align: center ;
  text-overflow: ellipsis ;
  white-space: nowrap ;
  overflow: hidden ;
}

.editor {
  display: flex ;
  margin-top: 10px ;
  flex: 1.25 ;
  padding: 10px ;
  border: $border ;
  font-family: 'Roboto Mono', sans-serif ;
  border-radius: 5px ;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5) ;

  span {
    display: flex ;
    align-items: center ;

    label {
      margin-right: 10px ;
    }
  }
}

.add-note,
.remove-note {
  position: fixed ;
  top: 0 ;
  left: 0 ;
  width: 100% ;
  height: 100% ;
  z-index: 100000 ;
  background: rgba(0, 0, 0, 0.6) ;

  &--content {
    position: absolute ;
    top: 50% ;
    left: 50% ;
    transform: translate(-50%, -50%) ;
    border-radius: 10px ;
    border: $border ;
    padding: 5px 10px ;
  }
}

.add-note {
  input,
  textarea {
    margin-bottom: 5px ;
    padding: 10px 15px ;
    font-size: 20px ;
    border: $border ;
    border-radius: 5px ;
    width: 100% ;
    font-family: 'Roboto', sans-serif ;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.5) ;
  }

  &--content {
    background: rgb(180, 180, 180) ;
    width: 90% ;
    height: 90% ;
  }
      
  &--title {
    text-transform: uppercase ;
    font-weight: bold ;
    font-size: 40px ;
    border-bottom: addBorder(3px, rgb(0, 0, 0)) ;
    text-align: center ;
    margin-bottom: 5px ;
  }

  &--body-textarea {
    height: 300px ;
    text-align: top ;
  }

  &--submit {
    font-size: 22px ;
    font-weight: bold ;
    background: rgb(0, 255, 0) ;
    color: $primary-color ;
    border: $border ;
    border-radius: 5px ;
    padding: 10px 15px ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5) ;
    cursor: pointer ;
  }
}

.remove-note {
  color: $primary-color ;

  &--content {
    background: rgb(255, 0, 0) ;
  }

  &--buttons {
    display: flex ;
    justify-content: center ;
    gap: 10px ;
  }

  &--delete-button,
  &--cancel-button {
    font-size: 16px ;
    font-weight: bold ;
    border: none ;
    border-radius: 5px ;
    padding: 7px 14px ;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5) ;
    opacity: 0.9 ;
    cursor: pointer ;
  }

  &--delete-button:hover,
  &--cancel-button:hover {
    opacity: 1 ;
  }
}

textarea {
  resize: none ;
}